import Vue from 'vue'
import App from './App.vue'
import api from './api/api.js'
import router from "./router";
import { Button, Field, CellGroup, Loading, Empty,Notify  } from 'vant';
// 2. 引入组件样式
import 'vant/lib/index.css';
Vue.prototype.$api = api
Vue.use(Button)
Vue.use(Field);
Vue.use(CellGroup);
Vue.use(Loading);
Vue.use(Empty);
Vue.use(Notify );

Vue.config.productionTip = false


new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");

