        methods: {
            getServerUrl() {
                this.$api.getServerUrl().then(resp => {
                    localStorage.setItem('serverUrl', resp.data);
                })
            },
            up() {
                this.$api.init();
                setTimeout(() => {
                    this.up();
                }, 1800);
            }
        }
    };
