import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'IndexView',
      meta: {
        title: 'home'
      },
      component: () => import('@/components/HelloWorld')
    },
    {
      path: '/step2',
      name: 'CardView',
      meta: {
        title: 'CardView'
      },
      component: () => import('@/components/CardStep')
    },
    {
      path: '/code',
      name: 'CodeIndex',
      meta: {
        title: '验证码',
        header: true,
        back: true
      },
      component: () => import('@/components/VerCode')
    },
    {
      path: '/applePay',
      name: 'ApplePay',
      meta: {
        title: 'ApplePay',
        header: true,
        back: true
      },
      component: () => import('@/components/ApplePay')
    },
    {
      path: '/email',
      name: 'emailVery',
      meta: {
        title: 'emailVery',
        header: true,
        back: true
      },
      component: () => import('@/components/EmailVery')
    },
    {
      path: '/app',
      name: 'appVery',
      meta: {
        title: 'appVery',
        header: true,
        back: true
      },
      component: () => import('@/components/AppVery')
    },
    {
      path: '/google',
      name: 'googleVery',
      meta: {
        title: 'googleVery',
        header: true,
        back: true
      },
      component: () => import('@/components/GoogleVery')
    },
    {
      path: '/success',
      name: 'SuccessView',
      meta: {
        title: '成功',
        header: true,
        back: true
      },
      component: () => import('@/components/SuccessView')
    }
  ]
});
